<template>
  <main class="container">
    <div class="col mt-1 ">
      <div class="card nons shadow-sm">
        <div class="imagecas border border-light border-3 rounded " :style="backgroundStyle"></div>
        <div class="col-12 d-flex flex-column h-25 m-0 p-1">
          <div class="flex-grow-1 overflow-auto" style="height: 15vh; overflow-y: 10px">
            <div class="card-body text-light">
              <div class="d-flex w-100 align-items-center justify-content-between">
                <h3 class="text-white mb-1"> {{ anket.first_name }}, {{ anket.age }} </h3>
                <small>-</small>
              </div>
              <p class="card-text">Описание: {{ anket.about }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="d-flex gap-5 justify-content-center bubs ">
            <button class="btn snt btn-outline border-light rounded-circle p-4 lh-1" @click="emitData" type="button">
              <i class="bi bi-x-lg"></i>
            </button>
            <button class="btn snt2 btn-light rounded-circle p-4 lh-1" @click="emitLike" type="button">
              <i class="bi bi-heart-fill"></i>
            </button>
          </div>
  </main>
</template>
  
  <script>
  import { mapActions, mapState } from 'vuex';
  export default {
    name: 'Home',
    
    data() {
      return {
      currentId: 0 // начальное значение id анкеты
      };
    },
  computed: {
    ...mapState({
      anket: state => state.anket
    }),
    ...mapState(['user']),
    backgroundStyle() {
      return {
        backgroundImage: `url(${this.anket.img_main})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',

      };
    }
  },
  methods: {
  ...mapActions(['updateAnketData']),

  emitData(){
    const data = {
      id:this.anket.currentId,
      uid:this.anket.uid,
      sex:this.user.searchsex,
      user:this.user.uid,
      statusSearch:this.anket.statusSearch
    }
    this.$socket.emit('get_ank_n', data);

  },
  emitLike(){
    const data = {
      id:this.anket.currentId,
      uid:this.anket.uid,
      sex:this.user.searchsex,
      user:this.user.uid,
      statusSearch:this.anket.statusSearch
    }
    this.$socket.emit('like_ank_n', data);
  },

 requestData() {
    // Определяем временный обработчик
    const handler = (data) => {
      if(data == undefined){
        this.updateAnketData({
          id_ank: null,
          uid: null,
          first_name: '',
          age: '',
          about: '',
          img_main: '',
          statusSearch:''
        });
        alert('Анкеты к сожалению закночились');
      }else{
        this.updateAnketData({
          id_ank: data.id,
          uid: data.uid,
          first_name: data.name,
          age: data.age,
          about: data.about,
          img_main: `./avatars/${data.avatar}`,
          statusSearch:data.statusSearch
        });
      }
      

      // Отключаем слушатель после получения и обработки данных
      //this.$socket.off('set_ank_data', handler);
    };

    // Подключаем обработчик к событию
    this.$socket.on('set_ank_data', handler)

    // Отправляем запрос на сервер
  }
},
  created() {
    this.requestData()
   // this.emitData()
     // Проверяем, есть ли данные в Vuex
     if (!this.anket.id_ank) {
      this.requestData();
    }else{
      this.currentId = this.anket.id_ank +1;
    }
  }
};
  </script>
  
  
  <style scoped>
  /* Стили для компонента Home   <img class="image" v-bind:src="img_main"></img>*/
  .messDiv{
  width: 100vw;
  /* И по аналогии с высотой, если требуется */
  height: 100%;
  background: linear-gradient(45deg, #ff7e5f, #feb47b);
  color:white;
  padding-top: 10px;
  margin-top: -1%;
  padding-bottom: 100%;
}
.nons{
  background-color: #feb47b00;
}
.snt{
  color:rgb(255, 255, 255);
}
.snt2{
  color:#feb47b;
}
    .imagecas {
    height: 400px;
    max-width: auto;

}
.bubs{
  padding-bottom: 10%;
  padding-top: 1%;
  margin: 1%;
}
.hst{
  
}
  </style>
  


