<template>
     <main class="container">
      <div class="hst p-3 rounded mt-3">
        <h1 class="text-center">Уведомления</h1>
      </div>
      <br>
      <div class="list-group" style=" --bs-list-group-border-color: #eeeeee;">
        <a v-for="user in userList" href="#" class="list-group-item list-group-item-action d-flex gap-3 py-3 nons" aria-current="true">
        <img :src="getImageUrl(user.avatar)" alt="twbs" width="64" height="64" class="rounded-circle flex-shrink-0">
        <div class="d-flex gap-2 w-100 justify-content-between">
          <div>
            <h6 class="mb-0">Взаимная симпатия с {{ user.name }}</h6>
            <a class="mb-0 opacity-75" :href="telegramProfileUrl(user.username)">Написать</a>
          </div>
          
        </div>
        </a>
      </div>
    </main>
  </template>
  
  <script>
  import { mapActions, mapState } from 'vuex';
  export default {
    name: 'Home',
    data() {
      return {
      userList: [] // начальное значение id анкеты
      };
    }
    ,computed: {
      ...mapState(['user']),  
    },
    methods:{
      requestData() {
        this.$socket.emit('get_notification', { uid: this.user.id});
        const handler = (data) => {
         this.userList = data
        };
      this.$socket.on('set_not', handler)
      },
      getImageUrl(image) {
      return `./avatars/${image}`;
    },
    telegramProfileUrl(username) {
      return `https://t.me/${username}`;
    }

    },
    mounted() {
      this.requestData();
    },
  }
  </script>
  
  <style scoped>
.messDiv{
  width: 100vw;
  /* И по аналогии с высотой, если требуется */
  height: 100%;
  background: linear-gradient(45deg, #ff7e5f, #feb47b);
  color:white;
  padding-top: 10px;
  margin-top: -1%;
  padding-bottom: 100%;
}
.nons{
  background-color: #fff;
  color:#ff7e5f;
}

.hst{
  background-color: #fff;
  color: #feb47b;
}
  </style>
  


