<template>
     <main class="container messDiv">
      <div class=" p-5 rounded mt-3">        
        <div class="col-md-7 col-lg-8"  >
          <div class="profile-header">
            <img
               :src="user.avatar || './avatars/noAvatar.jpg'"
                alt="Avatar"
                @click="triggerFileInput"
                style="cursor: pointer; border: 5px solid; border-color: aliceblue;"
              >
          </div>
        <h4 class="mb-3">Заполните ваш профиль</h4>
        <form @submit.prevent="handleSubmit" class="needs-validation">
          <div class="row g-3">
            <div class="col-12">
              <label for="firstName" class="form-label">Ваше имя</label>
              <input v-model="user.name" type="text" class="form-control" placeholder="" required="" pattern="[A-Za-zА-Яа-яЁё\s]+" title="Только буквы и пробелы">            </div>

            <div class="col-md-5">
              <label for="country" class="form-label">Укажите ваш возраст</label>
              <input v-model="user.age" type="number" class="form-control" placeholder="" required="" inputmode="numeric" pattern="\d+" title="Только цифры">            </div>

            <div class="col-md-5">
              <label class="form-label">Ваш пол</label>
              <select v-model="user.sex" class="form-select" id="sex" required="">
                <option value="">Выбор ...</option>
                <option value="1">Мужчина</option>
                <option value="2">Девушка</option>
              </select>
            </div>

            <div class="col-12">
              <label for="address" class="form-label">Описание</label>
              <input v-model="user.about" type="text" class="form-control" placeholder="Люблю гулять по Москве" required="">
            </div>

            <div class="col-md-5">
              <label class="form-label">Вы ищите</label>
              <select v-model="user.searchsex" class="form-select" id="search" required="">
                <option value="">Выбор ...</option>
                <option value="2">Девушек</option>
                <option value="1">Мужчин</option>
              </select>
            </div>
          </div>
          <input
            type="file"
            ref="fileInput"
            style="display: none;"
            @change="handleFileChange"
            accept="image/png, image/jpeg, image/jpg"
          >
          <hr class="my-4">

          <button class="w-100 btn btn-lg" type="submit">Сохранить</button>
        </form>
      </div>
      </div>
      <br>
      <br>
      <br>
      <br>
    </main>
  </template>
  
  <script>
  import { postData } from '../utils/api';
  import { mapActions, mapState } from 'vuex';
  export default {
  name: 'Home',
  data() {
    return {
      event_avatar: '',
      tg:window.Telegram.WebApp,
    };
  },
  computed: {
    ...mapState(['user']),
    
  },
  methods: {
    ...mapActions(['updateUserData']),
    
    // Метод для имитации клика по input
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
    handleSubmit() {
    this.updateUserData(this.user);
      if(this.user.name && this.user.avatar && this.user.searchsex && this.user.sex && this.user.sex){
        this.uploadImage();
      }else{
        console.log(this.user)
        console.log('Данных не хватает для создания анкеты');
      }
    },
    validateName() {
      const regex = /^[A-Za-zА-Яа-яЁё\s]+$/;
      if (!regex.test(this.user.name)) {
        this.user.name = this.user.name.replace(/[^A-Za-zА-Яа-яЁё\s]/g, '');
      }
    },
    validateAge() {
      this.user.age = this.user.age.replace(/[^\d]/g, '');
    },
    // Метод, вызываемый при выборе файла
    handleFileChange(event) {
      const file = event.target.files[0];
      if (file && ['image/jpeg', 'image/png', 'image/jpg'].includes(file.type)) {
        console.log('Тип файла соответстует');
        this.previewImage(file);
        this.event_avatar = file
      } else {
        alert('Only JPG and PNG files are allowed!');
      }
    },
    // Предварительный просмотр изображения
    previewImage(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.user.avatar = e.target.result;
      };
      reader.readAsDataURL(file);
    },

    // Загрузка изображения на сервер
    uploadImage() {
    if (!this.user.avatar) {
      alert('Пожалуйста, выберите изображение для загрузки.');
      return;
    }
   postData('/upload',{data:this.user,token:this.user.token})
    .then(data => {
      if(data.message){
        this.user.id = data.newId
        this.user.token = data.token
        this.currentComponent = 'Ankets';
      }
    })
    .catch(error => console.error('Error:', error));
  },
  watch: {
    'user.name': 'validateName',
    'user.age': 'validateAge',
  },
},
  
  created() {
   
  }
};
  </script>
  
  <style scoped>
  /* Стили для компонента Home */
  .messDiv{
  width: 100vw;
  /* И по аналогии с высотой, если требуется */
  height: 100%;
  background: linear-gradient(45deg, #ff7e5f, #feb47b);
  color:white;
  margin-top: -10%;
}
.messDiv h1 {
  text-align: center;
  margin-top: 50%;
}
.messDiv p {
  text-align: center;
  margin-top: 120%;
}
.messDiv button {
  text-align: center;
  color: #feb47b;
  width: 300px;
  background-color:aliceblue;
}
.messDiv input, select {
  color:#feb47b;
  background-color: aliceblue;
  border-color: aliceblue;
  text-align: center;
}
  .profile-header {
      text-align: center;
      margin: 10px 0;
    }
    .profile-header img {
      border-radius: 50%;
      width: 200px;
      height: 200px;
      object-fit: cover;
    }
    .profile-info {
      text-align: center;
      margin: 20px 0;
    }
    
  </style>
  


